#contact {
  width: 100%;
  height: 60vh;
  background: $bg-footer;
  color: $fontacc-color;
}

.footer-contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 100%;
}
.our-contact,
.quick-links,
.latest-posts,
.recent-news {
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin: 20px;
  width: 50%;
  height: 100%;
  text-align: left;

  h3 {
    font-size: 1.2em;
    text-transform: uppercase;
    align-self: flex-start;
    margin: 50px 0;
  }
  p,
  a {
    color: $fontacc-color;
    font-size: 0.9em;
    padding: 5px;
  }
}

.footer {
  width: 100%;
  height: 7vh;
  background: $bg-footeracc;
  p {
    padding: 10px;
  }
}
