header {
  width: 100%;
  color: $font-color;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: $bgacc-color;
}

nav {
  height: 7vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $font-color;
  font-family: "Poppins", sans-serif;
}

.logo {
  text-transform: uppercase;

  span {
    display: block;
    line-height: 1;
    &:nth-child(1) {
      font-size: 1.5em;
    }
    &:nth-child(2) {
      font-size: 0.7em;
      text-align: right;
    }
  }
}

.navbar {
  display: flex;

  li {
    list-style: none;
    margin-left: 10px;

    a {
      font-size: 0.7em;
      text-transform: uppercase;
      padding: 15px;

      &:hover {
        background-color: $hover-color;
        color: $fontacc-color;
      }
    }
  }
  .active {
    background-color: $bg-color;
    color: $fontacc-color;
  }
}
