#services {
  width: 100%;
  height: 80vh;
  font-family: "Poppins", sans-serif;
  text-align: center;
  color: $font-gray;
  margin: 50px 0 100px 0;

  h2 {
    font-size: 1.9em;
  }
  p {
    font-size: 1em;
  }
}

.row {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card {
  padding: 10px;
  margin-left: 15px;
  border-radius: 15px;
  background: $bg-gray;
  box-shadow: -6px -6px 20px rgba(255, 255, 255, 0.1),
    6px 6px 20px rgba(0, 0, 0, 0.1);

  color: $font-gray;

  .card-icon {
    font-size: 3em;
    margin-bottom: 15px;
  }

  .card-title {
    font-size: 1.2em;
    margin-bottom: 15px;
  }
  .card-desc {
    font-size: 0.9em;
    margin-bottom: 15px;
  }
  .btn {
    margin-top: 15px;
    display: inline-block;
    padding: 10px 15px;
    background: $bg-color;
    font-size: 0.7em;
    color: $fontacc-color;
  }
}
