#banner {
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0.7), rgba(3, 3, 3, 0.7)),
    url("../img/red-flowers-on-green-wooden-window-3100835.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: $fontacc-color;
  position: relative;
  font-family: "Poppins", sans-serif;
}

.banner-content {
  width: 45%;
  position: absolute;
  bottom: 25%;

  h2 {
    font-size: 2.3em;
    text-transform: uppercase;
    letter-spacing: 0.1em;
  }
  p {
    font-size: 1.1em;
    letter-spacing: 0.1em;
  }
}
