/* variables */
$font-color: #222277;
$hover-color: #222277;
$fontacc-color: white;
$bg-color: #222277;
$bgacc-color: white;
$font-gray: #666;
$bg-primary: #ebf5fc;
$bg-gray: #e6e6e6;
$bg-footer: #1b1b1b;
$bg-footeracc: #141414;

/* globale resets */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* body {
  background-color: $bg-primary;
} */

a {
  text-decoration: none;
}

.container {
  width: 85%;
  height: 100%;
  margin: 0 auto;
}
