#team {
  width: 100%;
  height: 70vh;
  font-family: "Poppins", sans-serif;
  background: $bg-color;
  color: $fontacc-color;
  text-align: left;
}

.team-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;

  img {
    width: 350px;
    height: 350px;
    margin-top: 50px;
  }
  .content {
    width: 45%;
    margin-top: 50px;

    h2 {
      text-transform: uppercase;
      font-size: 1.9em;
      padding-bottom: 15px;
    }
    p {
      font-size: 0.9em;
      padding-bottom: 10px;
    }
  }
}
